.container {
    /** 그리드 설정 **/
    display: grid;
    grid-auto-flow: column;
    column-gap: 10px;
    justify-content: flex-start;
    
    .btn {
        /** 테두리 설정 **/
        border-radius: 6px;

        /** 요소 크기 설정 **/
        width: 18px;
        height: 18px;

        /** 내부 여백 설정 **/
        padding: 5px 4px;

        /** 그리드 설정 **/
        display: flex;
    }

    .button_off {
        /** 배경이미지 설정 **/
        background-image: url(../../../images/icon/ic_check_off_18dp.svg);
        background-repeat: no-repeat;
        background-position: center;

        /** 배경색상 설정 **/
        background-color: #FFFFFF;

        /** 테두리 설정 **/
        border: 1px solid #E4E7EC;
    }

    .button_on {
        /** 배경이미지 설정 **/
        background-image: url(../../../images/icon/ic_check_on_18dp.svg);
        background-repeat: no-repeat;
        background-position: center;

        /** 배경색상 설정 **/
        background-color: #1A5DD1;

        /** 테두리 설정 **/
        border: none;
    }

    > span {
        /** 텍스트 설정 **/
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 20px;
        letter-spacing: -0.02em;
    }

    .span_on {
        /** 텍스트 색상 설정 **/
        color: #175CD3;
    }

    .span_off {
        /** 텍스트 색상 설정 **/
        color: #98A2B3;
    }
}